export async function fetchDailyReport(date: string, groupId?: string) {
  const params = new URLSearchParams({ date });
  if (groupId) params.append('groupId', groupId);
  const res = await fetch(`/api/reports/daily?${params.toString()}`);
  if (!res.ok) throw new Error('Fehler beim Laden des Tagesberichts');
  return res.json();
}

export function exportDailyReport(date: string, groupId?: string) {
  const params = new URLSearchParams({ date, format: 'csv' });
  if (groupId) params.append('groupId', groupId);
  window.open(`/api/reports/daily/export?${params.toString()}`);
}

